@import '../_styles/var.scss';

.container {

  .sidebarLeft,
  .sidebarRight {
    flex-grow: unset;
    padding: 15px;
    justify-content: center;
    align-items: center;
  }

  .content {
    flex-direction: column;
    padding: 15px 50px;
  }

  @include breakpoint(-sm) {
    flex-direction: column;

    .sidebarLeft {
      order: 1;
    }

    .sidebarRight {
      display: none;
    }

    .content {
      order: 0;
      padding: 15px 20px;
    }

  }


}
