* {
  padding: 0px;
  margin: 0px;
  font-family: "Segoe UI",-apple-system,BlinkMacSystemFont,Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}

html {
  min-height: 100%;
}

html,
body,
div {
  display: flex;
  flex-grow: 1;
}


body,
#root {
  flex-direction: column;
}

body {
  background: rgb(232, 232, 232);
  background: linear-gradient(54deg, rgba(232, 232, 232, 1) 15%, rgba(255, 255, 255, 1) 85%);
}



