@import '../../_styles/var.scss';

ul.list {
  &>li {
    float: left;
    padding: 10px;
    list-style-type: none;

    &:last-of-type {
      padding: 0px 10px;
      float: right;
      @include middle;
    }

    a {
      text-decoration: none;
      color: $colorinactive;

      &:hover {
        color: black;
        text-decoration: underline;
      }

    }
  }

}
