@import '../../_styles/var.scss';



.container {
  flex-direction: column;

  .info {
    align-items: center;

    @include breakpoint(-md) {
      flex-direction: column;
    }

    .img {
      min-width: 150px;
      min-height: 150px;
      width: 150px;
      height: 150px;
      flex-grow: unset;
      margin: 15px;

      img {
        width: 100%;
        height: auto;
        border-radius: 10px;
      }
    }

    .name {
      flex-direction: column;
      max-width: 700px;
      margin: 15px;

      @include breakpoint(-md) {
        margin: 0px;
      }
    }
  }

  .contact {
    flex-direction: column;
  }

}
